import React, {FC, useState} from 'react';
import s from './CardItem.module.scss'
import {Link} from 'react-router-dom';
import heart from 'assets/images/heart.webp'
import colorHeart from 'assets/images/heart-color.webp'

type PropsType = {
    image?: string
    title?: string
    subTitle?: string
    lastSeen?: string
    isLike?: boolean
}
export const CardItem: FC<PropsType> = ({title, subTitle, image, lastSeen, isLike}) => {
    const [like, setLike] = useState(false)
    const [over, setOver] = useState(false)
    const likeHandler = () => {
        setLike(!like)
    }
    const overHandler = (value: boolean) => {
        setOver(value)
    }
    return (
        <div className={s.cardItemContainer}>
            <div className={s.imgWrapper}>
                <img src={image} alt="card-item-image" className={s.img}/>
            </div>
            <div className={s.text}>

                <Link to={'/home'} className={s.title}>{title}</Link>

                <Link to={'/home'} className={s.subTitle}>{subTitle}</Link>
            </div>
            <button className={s.likeBtn}
                    onMouseOver={() => overHandler(true)}
                    onMouseLeave={() => overHandler(false)}
                    onClick={likeHandler}>
                {isLike && (like || over ? <img src={colorHeart} alt="like"/> :
                    <img src={heart} alt="like" className={s.btnImg}/>)}
            </button>
        </div>
    );
};

